import React from "react";

export default function WrongBrowser() {
    return (
        <>
            <style>
                {`
                @import url('../dist/fonts/PressStart2P.ttf');
                html,
                body {
                margin: 0;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                }

                body {
                background-color: rgb(1, 2, 172);
                font-family: 'Press Start 2P', cursive;
                color: white;
                }

                .container {
                display:flex;
                align-items:center;
                width:100%;
                height:100%;
                }

                .msg {
                padding-left: 100px;
                padding-right: 100px;
                }

                p {
                text-align: left;
                }

                .continue {
                text-align: center;
                }

                .highlight {
                color: rgb(1, 2, 172);
                background-color: rgb(172, 173, 168);
                padding: 3px;
                text-align: center;
                width: 300px;
                }

                .blink {
                animation: blink 1s steps(2, start) infinite;
                }

                @keyframes blink {
                to {
                    visibility: hidden;
                }
                }`}
            </style>
            <div class="container">
                <div class="msg" align="center">
                    <p class="highlight">Internet Explorer or Edge</p>
                    <p>
                        A fatal exception IE/Edge has occured. The current
                        webpage will be terminated.
                    </p>
                    <p>
                        * Press any key to be redirected to google chromes
                        website to download a better browser.
                    </p>
                    <p>
                        * Click with your mouse to be redirected to the mozilla
                        firefox website to download a better browser.
                    </p>
                    <br />
                    <p class="continue">
                        Press any key or click with your mouse to continue{" "}
                        <span class="blink">_</span>
                    </p>
                </div>
            </div>
        </>
    );
}
